import { Component, Input, Output, EventEmitter, OnInit } from '@angular/core';
import { ValidationErrors } from '@angular/forms';
import { Router, ActivatedRoute } from '@angular/router';
//
import { MatDialog, MatDialogRef } from '@angular/material';
import { Observable } from 'rxjs';
import { TranslateService } from '@ngx-translate/core';
//
import { BaseRouteComponent } from '../../../../../routing/components/base-route-component';
import { Blog } from '../../models/blog';
import { BlogsService } from '../../services/blogs.service';
import { Brand } from '../../../ms-brands/models/brand';
import { CanDeactivateMixin } from '../../../../../ui/helpers/component-can-deactivate';
import { ConfirmDialogComponent, } from '../../../../../ui/modules/confirm-dialog/components/confirm-dialog/confirm-dialog.component';
import { ErrorHandlingService } from '../../../../../error-handling/services/error-handling.service';
import { HandledError } from '../../../../../error-handling/models/handled-error';
import { ImagesService } from '../../../../../ui/modules/images-card/services/images.service';
import { Mixin } from '../../../../../ui/helpers/mixin-decorator';
import { ToastrService } from '../../../../../error-handling/services/toastr.service';

const errorKey = 'Error';

const savedStyleMessageKey = 'Saved Style Message';

@Component({
  selector: 'new-blog',
  templateUrl: './new-blog.component.html',
  styleUrls: ['./new-blog.component.scss']
})
@Mixin([CanDeactivateMixin])
export class NewBlogComponent implements CanDeactivateMixin, OnInit {

  data: any = {
    name: "",
  };

brands: Array<Brand>;

categories: Array<Blog>;

// Begin Mixin code of the CanDeactivate class
unsavedChanges = false;

cancelBtnKey = 'No';

okBtnKey = 'Yes';

saveTitleKey = 'Discard Title';

saveMessageKey = 'Discard Message';

modalRef: MatDialogRef<ConfirmDialogComponent>;

canDeactivate: () => Observable<boolean> | boolean;

dataChanged: () => void;

// end

validationErrors: ValidationErrors;

//@Input() brands: Array<Brand>;TODO

//@Output() close = new EventEmitter();TODO

constructor(
  public activatedRoute: ActivatedRoute,
  public blogsService: BlogsService,
  public dialog: MatDialog,
  private errorHandlingService: ErrorHandlingService,
  public imagesService: ImagesService,
  public router: Router,
  private translate: TranslateService,
  private toastr: ToastrService
  ) {
}

ngOnInit() {
  this.brands = this.activatedRoute.snapshot.data.brands;
}

submit(data: Blog) {
  this.createBlog(data);
}

cancel() {
  this.close();
}

close(){
  this.router.navigate(this.activatedRoute.snapshot.data.closeRouteCommand, { relativeTo: this.activatedRoute });
}

createBlog(blogData: Blog) {
  for (const position in blogData.faces) {
    const face = blogData.faces[position];

    const subscription$ = this.imagesService.postImage(face.file).subscribe(response => {
      blogData.imgUrl = response.data.url;
      this.blogsService.postBlog(blogData).subscribe(response => {
        this.unsavedChanges = false;
        this.close();
        this.toastr.success(savedStyleMessageKey);
      },
        (error: HandledError) => {
          this.errorHandlingService.handleUiError(errorKey, error);
          this.validationErrors = error.formErrors;
        });
    },
      (error: HandledError) => {
        this.errorHandlingService.handleUiError(errorKey, error);
        this.validationErrors = error.formErrors;
      });
  }
}
}
